import { Icon, Radio, Spin, Row, Col, Empty } from 'antd';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { firestoreConnect } from 'react-redux-firebase';
import { compose } from 'redux';
import ItinCard from "./ItinCard";
import ItineraryTable from './ItineraryTable';
import ResizeContainer from '../../Utilities/ResizeContainer';
import QueueAnim from 'rc-queue-anim';
import {grid} from './MyItineraryViewer.module.scss'

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class MyItineraryViewer extends Component {

  constructor(props) {
    super(props)
    this.state = {
      view: "2"
    }
  }

  onChange = (e) => {
    this.setState({ view: e.target.value })
  }


  render() {
    return (
      <div style={{ width: "100%" }}>
        <RadioGroup onChange={this.onChange} defaultValue="2">
          <RadioButton value="2"><Icon type="project" /></RadioButton>
          <RadioButton value="1"><Icon type="profile" /></RadioButton>
        </RadioGroup>

        {this.state.view === "1" &&
          <div style={{ marginTop: "20px", marginBottom: "20px" }}>
            <ItineraryTable loading={!this.props.auth.isLoaded} itineraries={this.props.itineraries}></ItineraryTable>
          </div>
        }

        {this.state.view === "2" &&
          <div className={grid} style={{  marginTop: "20px", width: "100%" }}>
            {/* <QueueAnim type={['bottom']} duration={[450, 0]} interval={[50, 0]}> */}
              {
                this.props.auth.isLoaded && this.props.itineraries && this.props.itineraries.length > 0 && this.props.itineraries.map((itin) => (
                  <ItinCard key={itin.id} itin={itin} />
                ))
              }
              {
                (!this.props.auth.isLoaded || !this.props.itineraries) &&
                <Row type="flex" justify="center" align="middle">
                  <Col span={24}>
                    <Spin />
                  </Col>
                </Row>
              }
            {/* </QueueAnim> */}
            {
              this.props.auth.isLoaded && this.props.itineraries && this.props.itineraries.length < 1 &&
              <Empty />
            }
          </div>
        }
      </div>
    )
  }
}

const mapStateToProps = (state, ownProps) => {
  return {
    ...ownProps,
    auth: state.firebase.auth,
    itineraries: state.firestore.ordered['users/' + state.firebase.auth.uid + '/itineraries']
  }
}

export default compose(
  connect(mapStateToProps, null),
  firestoreConnect(props => [{
    collection: 'users/' + props.auth.uid + '/itineraries',
    orderBy: ["editedAt", "desc"]
  }]),
)(MyItineraryViewer)