import { Col, Row, Spin } from 'antd';
import React, { Component } from 'react';
import {row} from './ResultList.module.scss';
import { connect } from 'react-redux';
import LoadingCard from './LoadingCard';
import QueryCard from './QueryCard';
import {grid} from './ResultList.module.scss'

class ResultList extends Component {

    render() {

        let loading = new Array(15).fill(15);
        let loadingCards = loading.map((temp, index) => (
            <LoadingCard key={index} />
        ))

        return (
            <div className={grid}>
                {/* <QueueAnim type={['bottom']} duration={[450, 0]} interval={[100, 0]}> */}
                {
                    this.props.itineraries && this.props.itineraries.map((itin) => {
                        return (
                            // <Col xs={24} sm={12} md={8} lg={6} xl={6} style={{flexGrow: 1, minWidth: "300px",padding: "1em"}}>
                                <QueryCard key={itin.id} itin={itin} />
                            // </Col>
                        )
                    })
                }
                {
                    !this.props.itineraries && !this.props.loading && this.props.resultList &&
                    this.props.resultList.map((itin) => (
                        // <Col xs={24} sm={24} md={6} style={{flexGrow: 1, minWidth: "350px", padding: "1em"}}>
                            <QueryCard key={itin.id} itin={itin} />
                        // </Col>
                    ))  
                }
                {
                    !this.props.itineraries && this.props.loading &&
                    <Spin />
                }
                {/* </QueueAnim> */}
            </div>
        )
    }
}

const mapStateToProps = (state, ownProps) => ({
    resultList: state.query.formattedResults,
    loading: state.query.loading
})

export default connect(mapStateToProps, null)(ResultList);